import request from '@/helpers/request.js'

const URL = {
    GET_LIST: '/blog', //获取博客列表
    GET_DETAIL: 'blog/:blogId', //获取id 为 blogId 的博客详情
    CREATE: '/blog', //创建博客
    UPDATE: '/blog/:blogId', //修改博客 id 为:blogId 的博客
    DELETE: '/blog/:blogId' //删除博客 id 为:blogId 的博客
}

export default {
    getBlogs({ page = 1, userId, atIndex } = { page: 1 }) {
        return request(URL.GET_LIST, 'GET', { page, userId, atIndex })
    },
    getIndexBlogs({ page = 1 } = { page: 1 }) {
        return this.getBlogs({ page, atIndex: true }) //获取首页博客列表
    },
    getBlogsByUserId(userId, { page = 1, atIndex } = { page: 1 }) {
        return this.getBlogs({ userId, page, atIndex }) //获取用户id为userId的博客列表
    },
    getDetail({ blogId }) {
        return request(URL.GET_DETAIL.replace(':blogId', blogId))
    },
    UpdateBlog({ blogId }, { title, description, content, atIndex }) {
        return request(URL.UPDATE.replace(':blogId', blogId), 'PATCH', { title, description, content, atIndex })
    },
    DeleteBlog({ blogId }) {
        return request(URL.DELETE.replace(':blogId', blogId), 'DELETE')
    },
    CreateBlog({ title = '', content = '', description = '', atIndex = false } = { title: '', content: '', description: '', atIndex: false }) {
        return request(URL.CREATE, 'POST', { title, content, description, atIndex })
    }
}


